
import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import PullTo from 'vue-pull-to'
import { getMessage } from '@/api/api';

export default {
components: {
XHeader, ButtonTab, ButtonTabItem, PullTo, Panel, LoadMore
},
data () {
return {
type: '5',
list: [],
infiniteLoading: true,
infiniteTip: "正在加载",
iconLink: '', //下拉刷新的状态
}
},
mounted() {
this.loadData();
},
methods: {

loadData(){
let para = {};
getMessage(para).then((res) => {
this.list = res;
if(res.length<10){
this.loadNoData_State();
}
});
},

loadNoData_State(){
this.infiniteLoading = false;
this.infiniteTip = "暂无更多";
},

loadData_State(){
this.infiniteLoading = true;
this.infiniteTip = "正在加载";
},

infinite(loaded){
if (this.bottom >= 30) {
this.loadNoData_State();
return false;
}
let start = this.bottom + 1
for (let i = start; i < start + 10; i++) {
this.ajaData(i);
}
this.bottom = this.bottom + 10;
setTimeout(() => {

}, 600)
},

refresh(loaded) {
setTimeout(() => {
this.loadData_State();
this.loadData();
loaded('done');
}, 600);
},

stateChange(state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom';
} else if (state === 'loading') {
this.iconLink = '#icon-loading';
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish';
}
}
}
}



